html,
body {
    height: 100%;
}

body {
    font-family: 'Varela Round', sans-serif;
    font-size: 10pt;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    background: #faf7f3 !important;
}

h3 {
    margin: 0 0 2px 0;
    color: #3f454b;
}

#app {
    margin: 0;
    padding: 0;
}

/*Navbar*/
.navbar-brand-logo {
    height: 90px;
    margin: 7px 15px 7px 0;
}

li > a > i {
    font-size: 18px;
}

.navbar-company-switcher-logo {
    height: 25px;
}

/*Main*/
/*Main-title*/
.page-title-wrapper .title {
    font-size: 20px;
    color: #444;
}

/*Content wrapper*/
.content-wrapper {
    border: none;
    border-radius: 4px;
    padding: 0;
}

/*Content-box*/
.content-box {
    background-color: #fff;
    padding: 15px 20px;
}

.content-box-title {
    display: flex;
    margin-bottom: 15px;
}

.content-box-title h3 {
    font-size: 20px;
    margin-right: 10px;
}

.content-box-title i {
    font-size: 18px;
    color: #3f454b;
}

.sidebar {
    width: 200px;
    min-height: 100vh;
    display: block;
    vertical-align: top;
    padding: 20px;
    margin: 0;
    top: 65px;
    left: 0;
    background: #eae6e1;
    overflow: auto;
    border-radius: 0;
}

.ui-tabs-nav {
    margin: 0 0 10px 0;
    padding: 0;
}

.ui-tabs-nav li {
    clear: left;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    border-radius: 0;
    background: 0 0 !important;
    list-style: none;
    float: left;
    position: relative;
    top: 0;
}

.ui-tabs-nav li a {
    display: block;
    padding: 10px 5px 8px;
    margin: 0 0;
    font-size: 14px;
    font-weight: 400;
    color: #666;
    transition: all 0.5s;
    text-decoration: none;
    float: left;
    box-sizing: border-box;
    width: 85%;
}

ui-tabs-nav li a i {
    opacity: 0;
    margin-right: 0;
    transition: all 0.5s;
}

.content-wrapper {
    margin: 0;
    padding: 10px;
}

/*CRUD*/
.table-wrapper {
    background: #fff;
    padding: 40px 40px;
    margin: 30px 0;
    border-radius: 3px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}

.table-title {
    padding-bottom: 5px;
    /* background: #7f8892; */
    color: #7f8892;
    padding: 1px;
    margin-bottom: 10px;

    /* border-radius: 3px 3px 0 0; */
}

.table-title h2 {
    margin: 5px 0 0;
    font-size: 18px;
}

.table-title .btn-group {
    float: right;
}

.table-title .btn {
    color: #fff;
    float: right;
    font-size: 13px;
    border: none;
    min-width: 50px;
    border-radius: 2px;
    border: none;
    outline: none !important;
    margin-left: 10px;
}

.table-title .btn i {
    float: left;
    font-size: 21px;
    margin-right: 5px;
}

.table-title .btn span {
    float: left;
    margin-top: 2px;
}

table.table tr th,
table.table tr td {
    border-color: #e9e9e9;
    padding: 12px 15px;
    vertical-align: middle;
}

table.table tr th:first-child {
    width: 60px;
}

table.table tr th:last-child {
    width: 100px;
}

table.table-striped tbody tr:nth-of-type(odd) {
    background-color: #fcfcfc;
}

table.table-striped.table-hover tbody tr:hover {
    background: #f5f5f5;
}

table.table th i {
    font-size: 13px;
    margin: 0 5px;
    cursor: pointer;
}

table.table td:last-child i {
    opacity: 0.9;
    font-size: 22px;
    margin: 0 5px;
}

table.table td a {
    font-weight: bold;
    color: #566787;
    display: inline-block;
    text-decoration: none;
    outline: none !important;
}

table.table td a:hover {
    color: #2196f3;
}

table.table td a.edit {
    color: #ffc107;
}

table.table td a.delete {
    color: #f44336;
}

table.table td i {
    font-size: 19px;
}

table.table .avatar {
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 10px;
}

.pagination {
    float: right;
    margin: 0 0 5px;
}

.pagination li a {
    border: none;
    font-size: 13px;
    min-width: 30px;
    min-height: 30px;
    color: #999;
    margin: 0 2px;
    line-height: 30px;
    border-radius: 2px !important;
    text-align: center;
    padding: 0 6px;
}

.pagination li a:hover {
    color: #666;
}

.pagination li.active a,
.pagination li.active a.page-link {
    background: #03a9f4;
}

.pagination li.active a:hover {
    background: #0397d6;
}

.pagination li.disabled i {
    color: #ccc;
}

.pagination li i {
    font-size: 16px;
    padding-top: 6px;
}

.hint-text {
    float: left;
    margin-top: 10px;
    font-size: 13px;
}

/* Custom checkbox */
.custom-checkbox {
    position: relative;
}

.custom-checkbox input[type='checkbox'] {
    opacity: 0;
    position: absolute;
    margin: 5px 0 0 3px;
    z-index: 9;
}

.custom-checkbox label:before {
    width: 18px;
    height: 18px;
}

.custom-checkbox label:before {
    content: '';
    margin-right: 10px;
    display: inline-block;
    vertical-align: text-top;
    background: white;
    border: 1px solid #bbb;
    border-radius: 2px;
    box-sizing: border-box;
    z-index: 2;
}

.custom-checkbox input[type='checkbox']:checked + label:after {
    content: '';
    position: absolute;
    left: 6px;
    top: 3px;
    width: 6px;
    height: 11px;
    border: solid #000;
    border-width: 0 3px 3px 0;
    transform: inherit;
    z-index: 3;
    transform: rotateZ(45deg);
}

.custom-checkbox input[type='checkbox']:checked + label:before {
    border-color: #03a9f4;
    background: #03a9f4;
}

.custom-checkbox input[type='checkbox']:checked + label:after {
    border-color: #fff;
}

.custom-checkbox input[type='checkbox']:disabled + label:before {
    color: #b8b8b8;
    cursor: auto;
    box-shadow: none;
    background: #ddd;
}

/* Modal styles */
.modal .modal-dialog {
    max-width: 400px;
}

.modal .modal-header,
.modal .modal-body,
.modal .modal-footer {
    padding: 20px 30px;
}

.modal .modal-content {
    border-radius: 3px;
}

.modal .modal-footer {
    background: #ecf0f1;
    border-radius: 0 0 3px 3px;
}

.modal .modal-title {
    display: inline-block;
}

.modal .form-control {
    border-radius: 2px;
    box-shadow: none;
    border-color: #dddddd;
}

.modal textarea.form-control {
    resize: vertical;
}

.modal .btn {
    border-radius: 2px;
    min-width: 100px;
}

.modal form label {
    font-weight: normal;
}

.text-warning {
    font-size: 15px !important;
}

@media (min-width: 992px) {
    .nav-item {
        text-align: center;
    }
}
.navbg {
    background-color: rgb(81, 177, 180);
}

