.pagination {
    margin-top: auto;
    padding: 0 3.5rem;
}

.pagination:after {
    content: '';
    clear: both;
    display: table;
}

.pagination__btn--prev {
    float: left;
}

.pagination__btn--next {
    float: right;
}

.pagination li.active a.page-link {
    background-color: rgb(81, 177, 180);
}

#import-button {
    cursor: pointer;
}

.spinner,
.check,
.alert {
    text-align: center;
}

.spinner svg {
    margin-top: 20px;
    height: 6rem;
    width: 6rem;
    animation: 2s linear infinite rotate;
}

.check svg {
    height: 6rem;
    width: 6rem;
}

@keyframes rotate {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

.check-icon, .x-icon {
    width: 20px;
    height: 20px;

}

.btn-catalog {
    background-color: white;
    color: black;
    border: 0px;
}

.btn-config {
    background-color: black;
    color: white;
    border: 0px;
}

#dashboard-page {
    display: inline;
}

#settings-page {
    display: none;
}
